body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Playfair Display", serif;
  color: black;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
}

a {
  text-decoration: none;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
