body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Playfair Display", serif;
  color: black;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 320px;
  margin: 0 auto;
}
.page-header h2 {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin-bottom: 0;
  padding: 0 10px;
}
.page-header__pic {
  background-image: url("../img/covermob.svg");
  background-size: 320px 190px;
  background-repeat: no-repeat;
  width: 320px;
  min-height: 190px;
  margin-top: -35px;
}
@media (min-width: 768px) {
  .page-header {
    width: 768px;
  }
  .page-header h2 {
    font-size: 45px;
    line-height: 55px;
    padding: 0 20px;
    margin-top: 20px;
  }
  .page-header__pic {
    background-image: url("../img/cover.svg");
    width: 768px;
    min-height: 375px;
    background-size: 768px 375px;
  }
  .page-header__pic:hover,
  .page-header__pic:active {
    background-image: url("../img/coverpainted.svg");
  }
}
@media (min-width: 1300px) {
  .page-header {
    width: 1300px;
  }
  .page-header h2 {
    font-size: 75px;
    line-height: 90px;
    padding: 0 40px;
    margin-top: 20px;
  }
  .page-header__pic {
    width: 1300px;
    min-height: 660px;
    background-size: 1300px 660px;
    margin-top: -40px;
  }
}
@media (min-width: 1300px) {
  .site-list {
    list-style: none;
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
}
.main-nav {
  display: none;
}
@media (min-width: 1300px) {
  .main-nav {
    display: block;
    position: sticky;
    top: 0;
    z-index: 1;
  }
  .main-nav__list {
    list-style: none;
    display: flex;
    width: 1280px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    font-family: "Montserrat", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 55px;
    border: 10px solid black;
    margin: -24px auto 0;
    padding: 0;
    background-color: white;
  }
  .main-nav__list li {
    padding: 15px;
  }
  .main-nav__list a {
    color: black;
    padding: 20px 30px;
  }
  .main-nav a:hover,
  .main-nav a:active {
    text-decoration: line-through;
  }
}
